@import "./normalize.css";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  background-color: #e7e5e5;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  max-width: 1280px;
  margin: 0 auto;
}

.page__body {
  min-height: 100%;
  margin: 0 auto;
  min-width: 320px;
}

.page__container {
  background-color: #ffffff;
  margin: 0 auto;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  max-width: 1200px;
  min-width: 320px;
  width: 100%;
}
@media (min-width: 1200px) {
  .page__container {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.main__header {
  padding: 0 18px;
  text-align: center;
}

.main__header .section__title {
  margin-bottom: 30px;
  line-height: 28px;
}
@media (min-width: 768px) {
  .main__header .section__title {
    font-size: 21px;
    margin-bottom: 26px;
  }
}

.main-header {
  min-height: 56px;
  display: grid;
  position: relative;
}

.nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: #ffffff;
}

.nav__wrapper {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 768px) {
  .nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  .nav__list > :nth-child(3) {
    grid-column: 4;
  }

  .nav__list > :nth-child(7) {
    grid-column: 4;
  }
}
.nav__toggle {
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  border: none;
  width: 63px;
  height: 56px;
  background-color: inherit;
  cursor: pointer;
  z-index: 3;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
  background-position: center;
  background-repeat: no-repeat;
  width: 23px;
  height: 23px;
}

.nav--opened .nav__wrapper {
  display: block;
}

.nav--closed .nav__wrapper {
  display: none;
}

.nav--closed .nav__toggle::after {
  content: "";
  color: #81b3d2;
  background-image: url("../../img/icons/icon-menu.svg");
}

.nav--opened .nav__toggle::after {
  color: #ffffff;
  background-image: url("../../img/icons/icon-cross.svg");
}

@media (min-width: 768px) {
  .nav--opened .nav__toggle, .nav--closed .nav__toggle {
    display: none;
  }

  .nav__wrapper {
    position: static;
  }

  .nav--closed .nav__wrapper {
    display: block;
  }
}
