.field--iconed {
  position: relative;
}
.field--iconed::after {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  background-repeat: no-repeat;
  background-position: center;
}
.field--phone::after {
  background-image: url("../img/icons/icon-phone.svg");
