  padding-right: 65px;
}

.field__control {
  position: absolute;
  overflow: hidden;
  margin: 0;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  background: #ffffff;
}

.field__radio {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  margin-left: -62px;
  margin-top: -10px;
}

.field__checkbox {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  margin-left: -62px;
  margin-top: -10px;
}

.field__radio::after,
.field__checkbox::after {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  background-position: center;
  background-repeat: no-repeat;
}

.field__control:checked + .field__radio::after {
  background-image: url("../img/icons/marker-dot.svg");
}

.field__control:checked + .field__checkbox::after {
  background-image: url("../img/icons/marker-check.svg");
}

